import { connect  } from "react-redux"
import { useParams } from 'react-router-dom';
import Head from './components/head';

import LockUser from "./components/lockList"

function LockOtherUser(props: any) {
    const {OtherUserAdr}: { OtherUserAdr: string }= useParams();
    console.log(OtherUserAdr)
    return (
        <div>
            <Head />
            <LockUser ChainID={props.ChainID} UserAdr={OtherUserAdr}  isOtherUser={true} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LockOtherUser)

