import { useRef } from 'react'
import { Modal, ModalHeader, ModalContent, Button, ModalActions } from 'semantic-ui-react'
import web3 from "../../../../../ABI/web3";
import BigNumber from 'bignumber.js';
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import lockV2ABI from "../../../../../ABI/LockV2.json";
import { getChainInfo } from '../../../../../components/global/functions';
import LoadingReport from './LoadingOpen';
import { Toast } from 'antd-mobile'

interface LockInfo {
    id: number
    tokenAdr: string
    symbol: string
    tokenLogo: string
    balance: number
    valueRMB: number
    remainTime: number
    unlockTime: number
    betweenTime: number
    onceAmount: number
    user: string
}

interface Props {
    ChainID: string;
    open: boolean;
    onClose: () => void;
    lockInfo: LockInfo;
    userAdr: string;
    refreshLockList: (ChainID: string, userAdr: string) => void;
    isHome: boolean;
}

function LockInfoWindow({ userAdr, ChainID, open, onClose, lockInfo, refreshLockList, isHome }: Props) {

    const clam = async () => {
        if (lockInfo.remainTime > 0) { return Toast.show({ content: '未到期' }) }
        try {
            const chainInfo = await getChainInfo(ChainID)
            const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract.methods.clam(lockInfo.id).send({ from: userAdr, gas: '100000' })

                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        onClose()
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "开盒中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean; events: any; }) => {
                    if (receipt.status === true) {
                        refreshLockList(ChainID, userAdr)
                        let receiptValue = web3.utils.fromWei(receipt.events[1].raw.data, 'ether')
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', `开盒完成，收到${receiptValue}`)
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })

        } catch (error) {
            console.error("Error creating lock:", error);

        }
    }

    const LoadingReportRef = useRef<any>(null);


    return (
        <>
            <Modal
                size='mini'
                open={open}
                onClose={onClose}
                closeIcon
                style={{ position: 'fixed', top: '180px' }}
            >
                <ModalHeader style={{  fontSize: '15px',textAlign: 'center' }}>
                        礼品盒详情
                </ModalHeader>
                <ModalContent style={{padding:'10px'}}>
                    <div>礼品通证：{lockInfo.symbol}</div>
                    <div>礼盒余量：{lockInfo.balance}</div>
                    <div>礼盒余值：{lockInfo.valueRMB}</div>
                    <div>单次领取：{lockInfo.onceAmount}</div>
                    <div>间隔天数：{parseFloat((new BigNumber(lockInfo.betweenTime).toNumber() / 86400).toFixed(1))}天</div>
                    <div>可领倒计：{lockInfo.remainTime}天</div>
                    <div style={{ wordBreak: 'break-all' }}>用户地址：{lockInfo.user}</div>
                </ModalContent>
                {isHome !== true && <ModalActions style={{
                    display: 'flex', justifyContent: 'space-between', padding: '0.5em 0.5em', alignItems: 'center'
                }}>
                    <Button onClick={onClose} >
                        取消
                    </Button>
                    <Button positive
                        onClick={clam}
                    >
                        领 取
                    </Button>
                </ModalActions>
                }
            </Modal>
            <LoadingReport ref={LoadingReportRef} />
        </>
    )

}

export default LockInfoWindow;




